
.versions-table tr:first-child > th {
  border-top: none !important;
}
@media (max-width: 610px) {
.versions-table th:nth-child(2), .versions-table td:nth-child(2),
  .versions-table th:nth-child(5), .versions-table td:nth-child(5) {
    display: none;
}
}
.action-buttons {
  min-width: 120px;
}
@media (max-width: 360px) {
.action-buttons {
    min-width: 80px;
}
.action-buttons .aui-button:nth-child(2) {
      display: none;
}
}



