
.releases-add-version {
  background-color: whitesmoke;
  border-top: 1px solid #D3D3D3;
  border-bottom: 1px solid #D3D3D3;
  padding: 10px 0;
  margin: 20px 20px 0;
}
#add-version-fieldset {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end;
}
#add-version-fieldset .field-group {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    padding: 0 10px;
}
#add-version-fieldset .field-group label {
      display: block;
}
#add-version-fieldset .field-group input {
      display: -webkit-inline-box;
      display: -ms-inline-flexbox;
      display: inline-flex;
      max-width: none;
      width: 100%;
}
#add-version-fieldset .field-group .input-with-button {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
}
#add-version-fieldset .field-group .input-with-button input {
        margin-right: 5px;
        -webkit-box-flex: 1;
            -ms-flex: 1;
                flex: 1;
}
#add-version-fieldset .field-group .input-with-button a, #add-version-fieldset .field-group .input-with-button button {
        -webkit-box-flex: 0;
            -ms-flex: 0;
                flex: 0;
}
#add-version-fieldset .field-group#button-group {
      -webkit-box-flex: 0;
          -ms-flex: 0;
              flex: 0;
      -ms-flex-item-align: end;
          align-self: flex-end;
}
@media (max-width: 810px) {
#add-version-fieldset {
    display: block;
}
#add-version-fieldset .field-group {
      width: 100%;
}
}



